.PhotoGrid img{
    width: 100%;
    height: 100%;
    border-radius: 5px;
}

.PhotoGrid .GridElement{
    position: relative;
    float: left;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 31.333333%;
    background: #f9f9f9;
    margin: 20px 1%;
    -webkit-box-shadow: -4px 2px 10px rgba(0,0,0,.4);
            box-shadow: -4px 2px 10px rgba(0,0,0,.4);
    height: 300px;
}
.PhotoGrid .GridElement.add-photo{
    padding: 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
    
}
.PhotoGrid .GridElement.add-photo img{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #ccc;
    border-radius: 10px;
}


