.AlbumPreview {
  width: 300px;
  color: #595a5b;
  display: inline-block;
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-width: 25%;
}
.AlbumPreview input {
  font-size: 1.05em;
  outline: none;
  margin: 5px 0;
  padding: 5px 10px;
  border: 0;
  -webkit-box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.4) inset;
          box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.4) inset;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
/* .AlbumPreview .preview{
    padding: 20px;
    background: #f9f9f9;
    border-radius: 20px;
} */
.AlbumPreview .preview h3 {
  margin: 0;
  text-align: center;
}
.AlbumPreview img {
  display: block;
  width: 100%;
  height: 220px;
}
.AlbumPreview .edit-button {
  width: 30px;
  height: 30px;
  /* box-shadow: -3px 0px 3px rgba(0,0,0,.2) inset; */
  float: right;
  cursor: pointer;
  -webkit-transition: -webkit-transform 0.3s ease-in-out;
  transition: -webkit-transform 0.3s ease-in-out;
  -o-transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.AlbumPreview .edit-button:hover {
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px);
}
.AlbumPreview .edit-button img {
  width: 100%;
  height: 100%;
}
.AlbumPreview .preview p {
  margin: 0;
}
.preview figure {
  padding: 0;
  margin: 0;
  position: relative;
}
.preview figure:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0.3;
  width: 100%;
  height: 100%;
  background: #fff;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.preview .previewBox:hover figure::after {
  opacity: 0;
}

.preview .previewBox {
  cursor: pointer;
  padding: 10px;
  -webkit-box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.2);
          box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.preview figcaption {
  padding: 10px;
  position: absolute;
  bottom: 0;
  right: 0;
  color: #440b0b;
  font-size: 1.2em;
}



